
























import Vue from 'vue'
import { Component } from 'vue-property-decorator'

@Component
export default class ConfirmModal extends Vue {
  resolve!: Function
  reject!: Function
  confirmTitle = '   ??'
  modal = false
  public open() {
    this.modal = true
    return new Promise((resolve, reject) => {
      this.resolve = resolve
      this.reject = reject
    })
  }

  accept() {
    this.resolve(true)
    this.modal = false
  }

  decline() {
    this.resolve(false)
    this.modal = false
  }
}
